.profileHeader {
    position: relative;
}
.profileHeaderPhotos{
    margin-bottom: -5px ;
    position: relative;

}
.backgroundPhoto {
    width: 100%;
    height: 300px;
    border-radius: 12px 12px 12px 12px;
}
.alignPhoto{
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: auto;
    text-align: center;
    margin-bottom: 2.1rem;
    vertical-align: middle;
    display: inline;
    z-index: 0;
}
.profilePhoto {
    width: 185px;
    height: 185px;
    border-radius: 50%;
}
.profileName {
    font-size: 1.708875rem;
}

.profileShortInfo{
    margin-left: 5px;
    margin-right: 5px;
    background-color: #0f0f0f;
    display: flex;
    min-height: 100px;
    border-radius: 0 0 12px 12px;
    justify-content: space-between;
    padding: 1.5rem;
    align-items: center;

}

.profileShortInfo img {
    width: 40px;
    height: 40px;
    border-radius: 10px;
}

.socialIcons {
    display: flex;
    justify-content: space-between;
    width: 30%;
}
.socialIcons a {
    z-index: 1;
    cursor: pointer;
}
.profileShortInfoNumbers {
    display: flex;
    text-align: center;
    justify-content: space-between;
    font-size: 1rem;
    width: 20%;
    margin: 0;
}
.profileShortInfoNumbers h5,p {
    margin: 0;
}